 .wrapper{
     display: flex;
     justify-content: center;
     flex-wrap: wrap;
}
 .card{
    width: 200px;
     height: 200px;
     background: #f4ece6;
     padding: 30px;
     box-sizing: border-box;
     margin:20px;
     box-shadow: -10px 15px 20px rgba(131, 131, 131, 0.4);
     border-radius: 50%;
}

 .card-title{
     margin-top: 0;
     font-size: 16px;
     font-weight: 600;
     letter-spacing: 1.2px;
     text-align: center;
}
 .card-content{
     font-size: 14px;
     letter-spacing: 0.5px;
     line-height: 1.5;
}
 .card-btn{
     all: unset;
     display: block;
     margin-left: auto;
     border: 2px solid #FFF;
     padding: 10px 15px;
     border-radius: 25px;
     font-size: 10px;
     font-weight: 600;
     transition: all 0.5s;
     cursor: pointer;
     letter-spacing: 1.2px;
}
 .card-btn:hover{
    color:#02b875;
    background: #FFF;
}
 .card:nth-child(2) .card-btn:hover{
    color:#4181ee;
    background: #FFF;
}
 .card:last-child .card-btn:hover{
    color:#673ab7;
     background: #FFF;
}

.skill-img-container{
    margin-left: auto;
    margin-right: auto;
    height: 80px;
    width: 80px;
}

.skill-img{
    width: 100%;
    height: auto;
}

@media only screen and (min-width: 800px) and (max-width: 1500px){
    .skillsContainer{
        height: 750px;
    }
}

@media only screen and (min-width: 533px) and (max-width: 800px){
    .skillsContainer{
        height: 1240px;
    }
}

@media only screen and (max-width: 532px){
    .skillsContainer{
        height: 2200px;
    }
}