
#tablet-nav-overlay{
    position: fixed;
    z-index: 5;
    margin-right: -500px;
    top: 116px;
    right: 0px;
    width: 500px;
    height: 100vh;
    background-color: #e6dace;
    /* display: none; */
    transition: all 0.5s;
}

.overlay-active{
    margin-right: 0px !important;
    box-shadow: -10px 0px 20px rgba(131, 131, 131, 0.4);
}

.nav-toggle-btn{
    z-index: 20;
}

.active {
	display: block !important;
}

.tablet-nav{
    background-color: #e6dace;
    margin-top: 50px;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
}

.tablet-nav li{
    float: none;
}

.tablet-nav a{
    width: 100%;
}



@media only screen and (min-width: 850px) and (max-width: 1000px){
    .about-details-cover{
        width: 750px;
    }
    .picture-designation{
        letter-spacing: 2px;
    }
}

@media only screen and (min-width: 550px) and (max-width: 850px){
    .about-details-cover{
        display: block;
        /* margin-top: 150px; */
        width: 500px;
    }
    .aboutContainer{
        height: 1040px;
    }
}

