@media only screen and (max-width: 960px){
    .bullet-container{
        display: none;
    }
    h3.head {
        font-size: 12px;
    }
    .details-container{
        margin-left: 20px;
        padding-top: 30px;
        padding-bottom: 30px;
    }
    .basic-details-container{
        flex: 95%;
    }
    .nav-toggle-btn{
		display: block;
		position: absolute;
		right: 1rem;
	}
	.basic-details-container{
		background-color: white;
	}
	.nav-container{
		width: 50px;
		padding: 35px;
        flex: none;
	}
	.nav-container ul.desktop-nav{
		display: none;
	}
    .aboutContainer, .projectsContainer, .headingMargin, .experienceContainer{
        margin-top: 85px;
    }
	.nav-container li{
		float: none;
	}
    .edu-years{
        margin-right: 0px;
    }
    .education-details-cover{
        width: 90%;
    }
    .long-details-cover{
        width: 90%;
        text-align: justify;
    }
}

