  .steps-progress {
    width: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .steps-progress-head {
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
  }
  .steps-progress-head .head-item {
    padding: 0.5rem 0.5rem;
    margin: 0.3rem;
    letter-spacing: 2px;
    cursor: pointer;
  }
  .steps-progress .steps-list {
    width: 100%;
    position: relative;
    list-style: none;
    background-color: #f4ece6;
    box-shadow: -10px 15px 20px rgba(131, 131, 131, 0.4);
    padding: 20px;
  }
  .steps-progress .steps-list::before {
    display: inline-block;
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    width: 10px;
    height: 100%;
    border-left: 3px solid #fff;
  }
  .steps-progress .steps-list .steps-item {
    position: relative;
    width: 50%;
  }
  .steps-progress .steps-list .steps-item:hover:nth-child(even)::before {
    height: 100%;
  }
  .steps-progress .steps-list .steps-item:hover:nth-child(odd)::before {
    height: 100%;
  }
  .steps-progress .steps-list .steps-item:nth-child(even) {
    padding-right: 20px;
    text-align: right;
    float: left;
    clear: both;
  }
  .steps-progress .steps-list .steps-item:nth-child(even)::before {
    content: "";
    position: absolute;
    top: 0;
    right: -3px;
    width: 3px;
    height: 0;
    background-color: #0050ff;
    display: inline-block;
    transition: all 255ms ease-in-out;
  }
  .steps-progress .steps-list .steps-item:nth-child(even)::after {
    content: "";
    position: absolute;
    top: 0;
    right: -9px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #fff;
    display: inline-block;
  }
  .steps-progress .steps-list .steps-item:nth-child(odd) {
    float: right;
    padding-left: 20px;
    clear: both;
  }
  .steps-progress .steps-list .steps-item:nth-child(odd)::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 3px;
    height: 0;
    background-color: #0050ff;
    display: inline-block;
    transition: all 255ms ease-in-out;
  }
  .steps-progress .steps-list .steps-item:nth-child(odd)::after {
    content: "";
    position: absolute;
    top: 0;
    left: -6px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #fff;
    display: inline-block;
  }
  .steps-progress .steps-list .steps-item:not(:last-child) {
    padding-bottom: 1rem;
  }
  .steps-progress .steps-list .steps-item .location {
    margin-top: 0.2rem;
  }
  .steps-progress .steps-list .steps-item .periode {
    margin-top: 1rem;
    font-size: 0.8rem;
  }