.contact-form {
  width: 70%;
  display: flex;
  justify-content: space-between;
  background: #fff;
  margin-left: auto;
  margin-right: auto;
  box-shadow: -10px 15px 20px rgba(131, 131, 131, 0.4);
}
.contact-form > * {
  width: 50%;
}
.contact-form .first-container {
  background: #f4ece6;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact-form .first-container .info-container div h3 {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.2;
}
.contact-form .first-container .info-container div:first-of-type p {
  max-width: 260px;
}
.contact-form .first-container .info-container div p {
  font-size: 16px;
  line-height: 1.6;
}
.contact-form .second-container {
  padding: 30px;
}
.contact-form .second-container h2 {
  font-size: 30px;
  font-weight: 400;
  line-height: 1.2;
  text-align: center;
  margin-bottom: 20px;
}
.contact-form .second-container form {
  display: flex;
  flex-direction: column;
}
.contact-form .second-container form .form-group {
  margin-bottom: 10px;
}
.contact-form .second-container form .form-group * {
  min-height: 55px;
  border: 1px solid #f4ece6;
  padding: 0 20px;
}
.contactForm input:focus{
	border: 1px solid #e6dace;
}
.contact-form .second-container form .form-group label {
  display: flex;
  align-items: center;
  width: 100%;
  border: 1px solid #f4ece6;
  font-size: 16px;
  color: #333;
  text-transform: uppercase;
  margin-top: -1px;
}
.contact-form .second-container form .form-group:first-of-type input {
  width: 100%;
  margin-right: -5px;
}
.contact-form .second-container form .form-group input {
  width: 100%;
  font-size: 15px;
  margin-top: -2px;
}
.contact-form .second-container form .form-group textarea {
  width: 100%;
  min-height: 80px;
  resize: none;
  padding: 10px 20px;
  margin-top: -1px;
}
.contact-form .second-container form button {
  width: 200px;
  height: 50px;
  background: #f4ece6;
  font-size: 17px;
  font-weight: 600;
  text-transform: uppercase;
  border: 0;
  position: relative;
  left: calc(50% - 100px);
  cursor: pointer;
}
.contact-form .second-container form button:hover {
  border-radius: 10px;
  background-color: #e6dace;
}

.linkContact{
  color: black;
  text-decoration: none;
}
.linkContact:hover{
  color: black;
  text-decoration: none;
}
input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0; 
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

@media screen and (max-width: 800px) {
  .contact-form {
    width: 90vw;
  }
}
@media screen and (max-width: 700px) {
  .contact-form {
    flex-direction: column-reverse;
  }
  .contact-form > * {
    width: 100%;
  }
  .contact-form .first-container {
    padding: 40px 0;
  }
  .contactContainer{
    height: 900px;
  }
}