@keyframes onloadEffect3070 {
	0% {width: 100%;}
	50% {width: 0%;}
	100% {width: 12%;}
}

@keyframes onloadEffect4060 {
	0% {width: 100%;}
	50% {width: 0%;}
	100% {width: 35%;}
}

.float30-transition{
	width: 100%;
	height: 100%;
	background-color: #e6dace;
	animation: onloadEffect3070 2s ease-in-out 2s 1 normal forwards;
	will-change: auto;
}

.float40-transition{
	width: 100%;
	height: 100%;
	float: right;
	background-color: #e6dace;
	animation: onloadEffect4060 2s ease-in-out 1s 1 normal forwards;
	will-change: auto;
}

