@media only screen and (max-width: 960px){
	.project-picture{
		display: none;
	}
	.projectDesc {
		margin-top: 120px;
	}
	.project-files-button{
		margin-left: -10px;
		text-decoration: none;
		color: #0050ff;
		border-radius: 5px;
		padding-right: 10px;
		padding-left: 10px;
		padding-top: 5px;
		padding-bottom: 5px;
		transition: all 0.2s;
	}
	.project-container{
		height: max-content;
		width: 100%;
		border-radius: 10px;
	}
	.project-details-cover{
		width: 90%;
		margin-left: auto;
		margin-right: auto;
	}
	.project-details{
		border-radius: 10px;
	}
	.projectContainer{
		height: 400px;
	}
}

@media only screen and (max-width: 700px){
	.projectDesc {
		margin-left: 30px;
		margin-top: 100px;
		margin-right: 30px;
		margin-bottom: 20px;
	}
	.projectName {
		float: left;
		margin-left: 25px;
		margin-top: 31px;
		color: #0050ff;
		font-weight: 600;
	}
	.projName {
		font-size: 20px;
	}
	.blue-bar{
		height: 45px;
	}
}

@media only screen and (max-width: 500px){
	.projectContainer{
		height: 500px;
	}
}

@media only screen and (max-width: 360px){
	.projectContainer{
		height: 560px;
	}
}

