/*animation*/
.about-pic-container-enter-animation{
	animation: fade-in-right 0.5s ease-out 0s 1 normal both;
	position: relative;
}

.long-details-cover-enter-animation, 
.heading-cover-enter-animation, 
.education-details-cover-enter-animation{
