	z-index: 1;
}

.about-desc-enter-animation{
	animation: fade-in-emerge-right 0.5s ease-out 1s 1 normal both;
	position: relative;
	z-index: -1;
}
.project-details-enter-animation{
	animation: fade-in-right 1s ease-out 0s 1 normal both;
	position: relative;
}
.project-picture-enter-animation{
	animation: fade-in-emerge-right 0.5s ease-out 1s 1 normal both;
	position: relative;
}

.about-pic-container-exit-animation{
	animation: fade-out-right 0.5s ease-out 0s 1 normal both;
	position: relative;
}
.picture-connect-exit-animation{
	animation: fade-out-right 0.5s ease-out 0s 1 normal both;
	position: relative;
	z-index: -1;
	margin-left: auto;
    margin-right: auto;
}
.about-desc-exit-animation{
	animation: fade-out-emerge-right 0.5s ease-out 0s 1 normal both;
	position: relative;
	z-index: -1;
}
.long-details-cover-exit-animation, 
.heading-cover-exit-animation, 
.education-details-cover-exit-animation{
	animation: fade-out-right-dual 0.5s ease-out 0s 1 normal both;
}
.experience-details-cover-exit-animation, 
.skills-details-cover-exit-animation, 
.heading-cover-exit-animation-dual, 
.contact-cover-exit-animation{
	animation: fade-out-left 0.5s ease-out 0s 1 normal both !important;
	z-index: 1;
}
.project-details-exit-animation{
	animation: fade-out-right 0.5s ease-out 0s 1 normal both;
	position: relative;
}
.project-picture-exit-animation{
	animation: fade-out-emerge-right 0.5s ease-out 0s 1 normal both;
	position: relative;
}

.bg-exit-animation3070{
	width: 100%;
	animation: bg-exit3070 0.5s ease-out 0s 1 normal both !important;
}
@keyframes bg-exit3070{
	0% {width: 12%;}
	100% {width: 100%;}
}

.bg-exit-animation4060{
	width: 100%;
	animation: bg-exit4060 0.5s ease-out 0s 1 normal both !important;
}
@keyframes bg-exit4060{
	0% {width: 35%;}
	100% {width: 100%;}
}

.page-display-timeout{
	display: none !important;
}

@keyframes fade-in-right{
	from{left: -200%; opacity: 0;}
	to{left: 0%; opacity: 1;}
}
@keyframes fade-in-right-dual{
	from{left: -200%; opacity: 0;}
	to{left: 50%; opacity: 1;}
}
@keyframes fade-in-emerge-right{
	from{left: -45%; opacity: -3;}
	to{left: 0%; opacity: 1;}
}
@keyframes fade-in-left{
	from{left: 200%; opacity: 0;}
	to{left: 50%; opacity: 1;}
}
@keyframes fade-in-emerge-bottom{
	from{margin-top: -100px; opacity: 0; width: 0%; z-index: -10;}
	to{margin-top: 0px; opacity: 1; width: 100%; z-index: 1;}
}

@keyframes fade-out-right{
	from{left: 0%; opacity: 1;}
	to{left: -200%; opacity: 0;}
}
@keyframes fade-out-right-dual{
	from{left: 50%; opacity: 1;}
	to{left: -200%; opacity: 0;}
}
@keyframes fade-out-emerge-right{
	from{left: 0%; opacity: 1;}
	to{left: -45%; opacity: -3;}
}
@keyframes fade-out-left{
	from{left: 50%; opacity: 1;}
	to{left: 200%; opacity: 0;}
}
@keyframes fade-out-emerge-bottom{
	from{margin-top: 0px; opacity: 1; width: 100%; z-index: 1;}
	to{margin-top: -100px; opacity: 0; width: 0%; z-index: -10;}
}

.main-content-display-none{
	display: none;
}